<template>
    <div>
        <head-top webActive="cart"></head-top>
        <div class="bgGray" id="cart">
            <div class="container">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                    <el-breadcrumb-item>购物车</el-breadcrumb-item>
                </el-breadcrumb>
                <div class="cartBg mt15" v-if="list.length>0">
                    <div class="cartHead">
                        <div class="cartChoice">
                            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                        </div>
                        <div class="cartName">商品名称</div>
                        <div class="unitPrice">单价</div>
                        <div class="cartNum">数量</div>
                        <div class="cartCount">小计</div>
                        <div class="cartControl">操作</div>
                    </div>
                    <div class="cartList">
                        <div class="cartItem" v-for="(item,index) in list" :key="index">
                            <div class="cartChoice">
                                <el-checkbox v-model="item.checked" name="cartBox" @change="handleCheckedChange(item.number,item.cart_id,item.checked)"></el-checkbox>
                            </div>
                            <div class="cartName">
                                <div class="imgStandard cartPic"><img :src="item.pic"></div>
                                <div class='cartInfo'>
                                    <h3 class="color3">{{item.name}}</h3>
                                    <p class="f12">{{item.spec_name}}{{item.spec_unit}}</p>
                                </div>
                            </div>
                            <div class="unitPrice fb color3">￥{{item.price}}</div>
                            <div class="cartNum">
                                <el-input-number class="pt0" @change="numberChange(item.number,item.cart_id,item.checked)" :min="1" size="mini" v-model="item.number"></el-input-number>
                            </div>
                            <div class="cartCount fb colorPrice">￥{{ (item.price*item.number).toFixed(2)}}</div>
                            <div class="cartControl"><el-button class="cartDel" type="text" icon="el-icon-delete" @click="delCartGoods(item.cart_id)">删除</el-button></div>
                        </div>
                    </div>
                    <div class="cartFoot">
                        <div class="cartTotal">
                            合计（不含运费）：
                            <span><label>￥{{total}}</label>元</span>
                        </div>
                        <div class="cartBtn" @click="confirm">
                            <span>下单</span>
                        </div>
                    </div>
                </div>
                <div class="cartEmpty tc" v-else>
                    <img src="../../images/emptyCart.png" height="200" width="200"/>
                    <div class="mt30">您的购物车还是空的~</div>
                    <div class="mt30">
                        <router-link to="/home">
                        <el-button type="primary" plain>去逛逛</el-button>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {cartList,delCartGoods,changeCartGoodsnum} from '../../service/api'
    import headTop from '../../components/header/headTop';

    export default {
        name: 'cart',
        data(){
            return{
                checkAll: false,
                isIndeterminate: true,
                list:'',
                total:''
            }
        },
        components:{
            headTop
        },
        mounted(){
            this.getUserInfo();
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo',
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo','initCart'
            ]),
            async initData(){
                if (this.userInfo && this.userInfo.user_id){
                    let res = await cartList();
                    this.list = res.cartInfo;
                    this.calculate();
                }
            },
            handleCheckAllChange(val) {
                if(val){
                    //val = true
                    for(var i = 0 ; i < this.list.length ; i++){
                        this.list[i].checked = true;
                    }
                }else{
                    //val = false
                    for(var i = 0 ; i < this.list.length ; i++){
                        this.list[i].checked = false;
                    }
                }
                this.isIndeterminate = false;
                this.calculate();
            },
            async handleCheckedChange(number,cartId,isCheck) {
                var checkArr = document.getElementsByName("cartBox");
                var checked_counts = 0;
                for(var i = 0 ; i < checkArr.length; i++){
                    if(checkArr[i].checked){
                        checked_counts++;
                    }
                }

                var res = await changeCartGoodsnum(number,cartId,isCheck);
                if(true){//res.status!=12000
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    });
                }
                this.isIndeterminate = checked_counts==checkArr.length? false : true;
                this.checkAll = checked_counts==checkArr.length? true : false;
                this.initData();
                // this.calculate();
            },
            calculate(){
                var total = 0;
                for(var i = 0 ; i < this.list.length ; i++){
                    if(this.list[i].checked){
                        let price = this.list[i].price *100;
                        total +=   parseInt(this.list[i].number)*parseInt(price)/100;
                    }
                }
                this.total = total.toFixed(2);
            },
            async numberChange(number,cartId,isCheck){
                var res = await changeCartGoodsnum(number,cartId,isCheck);
                if(res.status!=12000){
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    });
                }
                this.initData();
                // this.calculate();
            },
            async confirm(){
                var checkArr = document.getElementsByName("cartBox");
                var checked_counts = 0;
                for(var i = 0 ; i < checkArr.length; i++){
                    if(checkArr[i].checked){
                        checked_counts++;
                    }
                }
                if(checked_counts==0){
                    //未选择任何产品
                    this.$message({
                        message: '您还未选择任何产品',
                        type: 'warning'
                    });
                }else{
                    this.$router.push({ path: '/order/confirm' });
                    //this.$router.push({ name: 'home', params: { userId: wise }})
                }
            },
            delCartGoods(cart_id){
                this.$confirm('确认删除该产品?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'info'
                }).then(async () => {
                    let res = await delCartGoods(cart_id);
                    if(res){
                        this.$message({
                            message: res.msg,
                            type: res.status==12000?'success':'warning'
                        });

                        let newList = await cartList();
                        this.list = newList.cartInfo;
                        this.calculate();
                        this.initCart();
                    }


                }).catch(() => {
                    // this.$message({
                    //     type: 'info',
                    //     message: '已取消删除'
                    // });
                });


                // let res = await delCartGoods(cart_id);
                // if(res){
                //     this.$message({
                //         message: res.msg,
                //         type: res.status==12000?'success':'warning'
                //     });
                // }
            }
        },
        created(){
        },
        watch:{
            userInfo: function (value) {
                if (value && value.user_id && !this.list) {
                    this.initData();
                }
            }

        }

    }

</script>

<style scoped>
    /*购物车*/
    #cart{padding:10px 0;min-height: 760px;}
    .cartBg{background-color: #fff;font-size: 14px;}
    .cartHead{display: flex;flex-wrap: nowrap;justify-content: space-between;padding:15px;border-bottom:1px solid #ddd;}
    .cartChoice{flex-shrink: 0;flex-basis: 60px;display: flex;text-align: left;}
    .cartName{display: flex;flex-wrap: nowrap;width:100%;flex-shrink: 1;}
    .unitPrice{text-align: center;flex-basis: 120px;flex-shrink: 0;}
    .cartNum{text-align: center;flex-basis: 150px;flex-shrink: 0;}
    .cartNum .numCount{margin:0 auto;}
    .cartCount{flex-basis: 120px;text-align: center;flex-shrink: 0;}
    .cartControl{text-align: right;flex-basis: 60px;flex-shrink: 0;}
    .cartItem{display: flex;flex-wrap: nowrap;justify-content: space-between;padding:15px;border-bottom: 1px solid #ddd;}
    .cartPic{width:80px;height: 80px;}
    .cartPic img{width:80px;height: 80px;}
    .cartInfo{padding-left: 15px;}
    .cartDel{color:#999;padding-top:0;}
    .cartDel:hover{color:#0bb8b2;}
    .cartFoot{height:56px;background-color: #f5f5f5;display: flex;flex-wrap: nowrap;justify-content: flex-end;border-bottom:1px solid #ddd;line-height: 56px;}
    .cartTotal{color:#333;padding-right: 15px;}
    .cartTotal span{color:#ff6600;}
    .cartTotal span label{color:#ff6600;font-size:28px;}
    .cartBtn{background-color:#ff6600;color:#fff;width:160px;font-size: 16px;text-align: center;}
    .cartBtn:hover{cursor:pointer;}
    .cartEmpty{text-align: center;font-size:28px;font-weight: bold;padding:30px 0;}


</style>



